
   .conatiner-progressSkills {
    display: block;
    width: 20rem;
    height: 13rem;
    position: fixed;
    top: 20rem;
    left: 10rem;
  }
  
  .skill-menu {
    width: 32rem;
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 24%;
    right: 16vw;
    align-items: flex-end;
  }
  
  .skill-item {
    height: 3rem;
    width: 50%;
    display: flex;
    position: relative;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
  }
  
  .skill-title {
    font-size: 0.75rem;
    padding-right: 4.5rem;
  }
  
  .skill-item.activeSkill .skill-title {
    font-size: 1.5rem;
  }
  
  .activeSkill {
    border-radius: var(--radius-left);
    cursor: default;
  }
  
  .skill-item.activeSkill:nth-child(1) {
    background: linear-gradient(270deg, #ffffff00, var(--cyan) 70%);
  }
  .skill-item.activeSkill:nth-child(2) {
    background: linear-gradient(270deg, #ffffff00, var(--blue) 70%);
  }
  
  .skill-icon {
    width: 5rem;
    height: 5rem;
    position: absolute;
    top: -0.2rem;
    left: 10rem 
  }
  
  .skill-sub-container-1 h3,
  .skill-sub-container-2 h3 {
    position: relative;
    margin: 0;
  }
  
  .skill-sub-container-1,
  .skill-sub-container-2 {
    display: flex;
    justify-content: right;
    gap: 2rem;
  }
  
  .level-container {
    display: flex;
    gap: 0.3rem;
  }
  
  .level-point {
    width: 3rem;
    height: 2rem;
    border-radius: 0.2rem;
  }
  
  .filled {
    background: linear-gradient(180deg, #236473, #25afce, #7ce7ff);
  }
  .unfilled {
    background: linear-gradient(180deg, #1c8298, #074654, #074654);
  }
  
  .skill-sub-container {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
  }

  @media (max-width: 1024px) {
    .skill-item.activeSkill .skill-title {
      font-size: 1.25rem;
    }
    .menu {
        width: 10rem;
        top: 30%;
        left: 50%;
        
      }
      .skill-menu {
        width: 35rem;
        right: 36vw;
       
      }
    
  
  }
  
  @media (max-width: 992px) {
    .skill-item.activeSkill .skill-title {
      font-size: 1rem;
    }
    .menu {
        width: 10rem;
        top: 30%;
        left: 50%;
        transform: translateY(-50%);
      }
    
  
  }
  